@font-face {
  font-family: 'PFBeauSansPro Regular';
  src: url('../fonts/PFBeauSansPro/PFBeauSansProReg.eot') format('embedded-opentype'),
  url('../fonts/PFBeauSansPro/PFBeauSansProReg.woff2') format('woff2'),
  url('../fonts/PFBeauSansPro/PFBeauSansProReg.woff') format('woff'),
  url('../fonts/PFBeauSansPro/PFBeauSansProReg.ttf') format('truetype'),
  url('../fonts/PFBeauSansPro/PFBeauSansProReg.svg') format('svg');
  font-weight: normal;
}
